<template>
	<view>
		<view class="box">
			<user-card :ovoUserInfo="ovoUserSelf"></user-card>
		</view>
		<view class="box box1">
			<view style="height: 50rpx;"></view>

			<view class="form-item" style="margin-bottom: 0;
			text-align: center;
			">

				<image :src="$kt.file.byPath('image/icon/email.svg')" style="width: 150rpx;height:150rpx" mode="widthFix">
				</image>
			</view>


			<view>

				<view class="form-item">


					<view class="form-item-input-box">
						<image :src="$kt.file.byPath('image/icon/at.svg')" class="form-item-input-icon" mode="widthFix">
						</image>

						<input class="form-item-input" @input="sendToInput" v-model.trim="sendTo" placeholder="请输入邮箱地址"
							type="sendTo"></input>

						<view @click="sendCode()" v-if="!issendToValid && !isCooling" style="opacity:.5"
							class="form-item-input-text">发送验证码</view>

						<view v-if="issendToValid && !isCooling" @click="sendCode()" style="color:#333"
							class="form-item-input-text btn">发送验证码</view>

						<view v-if="isCooling" style="color:#333" class="form-item-input-text btn">
						<text
						v-if="coolingTimeTime > 0">
						{{ coolingTimeTime }}s
						</text>
						<text v-else>
						*s
						</text>
						</view>

					</view>

					<view style="height: 30rpx;"></view>

					<view class="form-item-input-box">

						<view class="form-item-input-text">验证码</view>

						<input class="form-item-input" v-model="requestParam.verificationCode" @click="codeClick"
							maxlength="6" placeholder="请输入验证码" type="sendTo"></input>

					</view>


				</view>


				<view style="height: 40rpx;"></view>

				<view class="form-item" style="text-align: center;">
					<view style="width: 90%;display:inline-block">
						<kt-button ref="bindBtn" class="form-item-control" type="primary" @click="bindsendTo">
							<view>绑定</view>
						</kt-button>
						<view style="height: 10rpx;"></view>
						<view style="opacity: .7;
						font-size: 26rpx;
						">{{ requestParam.sendTo }}</view>

					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			sendTo: '',
			ovoUserSelf: {},
			// 是否邮箱验证通过
			issendToValid: false,
			codeDisabled: false,
			requestParam: {
				sendTo: '',
				verificationCode: '',
			},
			// 是否正在冷却中
			isCooling: false,
			// 冷却的时间戳
			coolingTime: 0,

			timer: null,

			// 还剩多少秒
			coolingTimeTime: 0,

			// 是否正在发送中
			isSending: false,

		}
	},
	created() {
		this.ovoUserSelf = this.$ovo.getSelf();
		// 从缓存中获取coolingTime
		this.coolingTime = uni.getStorageSync('coolingTime');
		if (this.coolingTime&&new Date().getTime() - this.coolingTime < 60000) {
			this.isCooling = true;

			this.timer=setInterval(() => {
				this.cooling();
			}, 1000);
		}

	},

	methods: {
		// 计算冷却时间
		cooling() {
			if (this.isCooling) {
				const now = new Date().getTime();
				const time = Math.floor((now - this.coolingTime) / 1000);
				console.log(time);
				if (time >= 60) {
					this.isCooling = false;
					this.coolingTimeTime = 0;
					clearInterval(this.timer);
				} else {
					this.coolingTimeTime = 60 - time;
					this.$forceUpdate();
				}
			}
		},

		codeClick() {
			if (this.issendToValid && this.requestParam.sendTo == '') {
				this.requestParam.sendTo = this.sendTo;
			}

			if (this.requestParam.sendTo == '') {
				this.codeDisabled = true;
				this.$refs.bindBtn.error('没有邮箱地址');
				return null;
			}
		},
		sendToInput(e) {
			this.sendTo = e.target.value;
			this.sendTo = this.sendTo.trim();
			// 使用正则表达式验证邮箱格式，代码如下
			const sendToRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
			if (!sendToRegex.test(this.sendTo)) {
				this.issendToValid = false;
				return false;
			}
			this.issendToValid = true;
		},

		sendCode() {
			if (this.isSending) {
				return null;
			}

			console.log('发送验证码');


			if (this.sendTo == '') {
				this.$refs.bindBtn.error('没有邮箱地址');
				return null;
			}

			if (!this.issendToValid) {
				this.$refs.bindBtn.error('邮箱地址格式不正确');
				return null;
			}

			this.isSending = true;
			this.requestParam.sendTo = this.sendTo;

			this.$kt.request.request({
				api: this.$api.system.verificationCode.send,
				data: {
					sendTo: this.sendTo,
					typeCode: "email",
					actionCode: "bindEmail"
				},
				stateSuccess: (res) => {
					this.$refs.bindBtn.success('发送成功');
					this.isSending = false;
					this.isCooling = true;
					this.coolingTime = new Date().getTime();
					this.coolingTimeTime = 60;
					// 将coolingTime放进缓存
					uni.setStorageSync('coolingTime', this.coolingTime);

					// 放进定时器
					this.timer = setInterval(() => {
						this.cooling();
					}, 1000);
				},
			});
		},

		bindsendTo() {
			if (this.requestParam.sendTo == '') {
				this.$refs.bindBtn.error('没有邮箱地址');
				return null;
			}

			if (this.requestParam.verificationCode == '') {
				this.$refs.bindBtn.error('没有验证码');
				return null;
			}


			this.$kt.request.request({
				api: this.$api.system.user.bindEmail,
				data: this.requestParam,
				stateSuccess: (res) => {
					this.$refs.bindBtn.success("绑定成功");

					this.$selfInfo.set(res.data);
					uni.navigateBack();
				},
				stateFail: (res) => {
					this.$refs.bindBtn.error("验证码错误");
				},
			});

		},
	}
}
</script>

